@charset "utf-8";

.tit_todo {
  text-align: center;
  padding-top: 30px;
}

#Todo {
  width: 800px;
  margin: 30px auto;
}
.input_todo {
  margin-bottom: 20px;
}
.list_todo li {
  background-color: #eee;
  border-bottom: 1px solid #ddd;
}

.list_todo li:last-child {
  border-bottom: none;
}

.list_todo .guid {
  height: 2px;
  width: 100%;
  background-color: blue;
}
.item_todo.done .txt_todo {
  text-decoration: line-through;
}

.item_todo {
  background-color: #eee;
  padding: 15px 10px;
}

.item_todo.drag {
  background-color: #4063bd;
}

.item_todo.drag .txt_todo {
  color: #fff;
}

.info_todo {
  position: relative;
  overflow: hidden;
  padding-right: 60px;
  margin-top: 5px;
}

.info_todo .btn_wrap {
  float: right;
}
.info_todo .btn_del {
  position: absolute;
  right: 0;
  top: 1px;
}

.drag_preview {
  opacity: 0.8;
}
